<template>
  <div>
    <van-nav-bar title="" left-text="" left-arrow />
    <div class="body">
      <!--  -->
      <van-field v-model="mobile" placeholder="" class="inp" right-icon="phone-circle-o" />
      <!--  -->
      <van-field
        v-model="pwd"
        placeholder=""
        :type="pwdisadmin?'password':'text'"
        class="inp"
        :right-icon="pwdisadmin?'closed-eye':'eye-o'"
        @click-right-icon="pwdisadmin = !pwdisadmin"
      />
      <!--  -->
      <van-field
        v-model="confirmpwd"
        placeholder=""
        :type="pwdisadmin1?'password':'text'"
        class="inp"
        :right-icon="pwdisadmin1?'closed-eye':'eye-o'"
        @click-right-icon="pwdisadmin1 = !pwdisadmin1"
      />
      <!--  -->
      <van-field v-model="nick" placeholder="" class="inp" right-icon="manager" />
      <!--  -->
      <van-field v-model="picCode" placeholder="" class="inp">
        <template #right-icon>
          <van-image :src="imgurl" @click="imgc" />
        </template>
      </van-field>
      <!--  -->
      <van-field
        v-model="citytext"
        placeholder=""
        class="inp"
        right-icon="location-o"
        @click="show = true"
      />
      <van-popup v-model="show" position="bottom" :style="{ height: '50%' }">
        <van-area
          show-toolbar
          title=""
          :area-list="columns"
          @cancel="show = false"
          @confirm="historyT"
        />
      </van-popup>
      <!--  -->
      <van-field v-model="code" placeholder="" class="inp">
        <template #button>
          <van-button size="small" type="primary" @click="codeBtn">{{ codetxt }}</van-button>
        </template>
      </van-field>
      <!--  -->
      <van-button color="linear-gradient(to right, #4bb0ff, #6149f6)" block @click="btn"></van-button>
      <p class="jump"><router-link to="/login/dl"></router-link></p>
    </div>
  </div>
</template>

<script>
import "@/assets/reset.css";
import area from "@/assets/area.js";
import axios from "axios";

export default {
  data() {
    return {
      mobile: "", //
      pwd: "", //
      confirmpwd: "", //
      codeKey:"", //key
      nick: "", //
      city: "", //
      code: "", //
      //   
      imgurl: "",
      //   
      pwdisadmin: true,
      pwdisadmin1: true,
      //   
      show: false,
      columns: area, //
      city: "",
      province: "",
      citytext: "",
      //
      codetxt: "",
      picCode: ""
    };
  },
  created() {},
  mounted() {
    //   
    this.imgc();
  },
  methods: {
    //   
    imgc() {
      this.codeKey =(new Date().getTime());
      let url = `https://api.it120.cc/small4/verification/pic/get`;
      this.imgurl = `${url}?key=${this.codeKey}`;
      console.log(this.codeKey);
    },
    historyT(arr) {
      //
      this.show = false;
      let list = arr
        .map(item => {
          return item.name;
        })
      this.city = list[1]
      this.province = list[0]
      this.citytext = list.join(" ");
      console.log(this.city,this.province);
      
    },
    btn() {
      //
      if (
        this.mobile == "" ||
        this.pwd == "" ||
        this.confirmpwd == "" ||
        this.imgcode == "" ||
        this.nick == "" ||
        this.code == ""
      ) {
        this.$toast.fail("");
        return false;
      }

      let phone = /^1[3,4,5,7,8]\d{9}$/;       //
      if (!phone.test(this.mobile)) {
        this.$toast.fail("");
        return false;
      }
      if (this.pwd != this.confirmpwd) {       //
        this.$toast.fail("");
        return false;
      }
      this.submitBtn()
      
    },
    codeBtn() {
      //
      if (this.mobile == "") {
        this.$toast.fail("");
        return false;
      }
      axios  //
        .post(
          `https://api.it120.cc/small4/verification/sms/get?mobile=${this.mobile}&&picCode=${this.picCode}&&key=${this.codeKey}`
        )
        .then(res => {
          if(res.data.code != 0){        //
             this.$toast.fail(res.data.msg);
             return false;
        }
         console.log(this.mobile,this.codeKey,this.picCode)
          let num = 10;
          let time = setInterval(() => {    //
            num--;
            if (num < 1) {
              clearInterval(time);
              this.codetxt = "";
              return false;
            }
            this.codetxt = `${num}s `;
          }, 1000);
        });
    },
    submitBtn(){
         this.$axios({   //
           url:"https://api.it120.cc/small4/user/m/register",
           params:{
              mobile:this.mobile,
              pwd:this.pwd,
              code:this.code,
              nick:this.nick,
              province:this.province,
              city:this.city
           }
         }).then(res=>{
            console.log(res.data)
            if(res.data.code != 0){        //
              this.$toast.fail(res.data.msg)
              return false;
            }
            this.$toast.success("");    //
            this.$router.push('/')
         })
    }
  }
};
</script>

<style scoped  lang="scss">
.body {
  width: 90%;
  margin: 0 auto;
  .inp {
    width: 100%;
    border: 1px solid #f5f5f5;
    background-color: #f5f5f5;
    border-radius: 0.2rem;
    margin-bottom: 0.4rem;
    img {
      width: 3rem;
    }
  }
  .jump{
    width: 100%;
    height: 50px;
    line-height: 50px;
    text-align: center;
  }
}
</style>



