<template>
    <div>
        <van-nav-bar title="" left-text="" left-arrow @click-left="back"/>
        <div class="body">
            <!--  -->
            <van-field v-model="mobile" placeholder="" class="inp" right-icon="phone-circle-o" />
            <!--  -->
            <van-field
                v-model="pwd"
                placeholder=""
                :type="pwdisadmin?'password':'text'"
                class="inp"
                :right-icon="pwdisadmin?'closed-eye':'eye-o'"
                @click-right-icon="pwdisadmin = !pwdisadmin"
            />
            <!--  -->
            <van-button color="linear-gradient(to right, #4bb0ff, #6149f6)" block @click="btn"></van-button>
            <p class="jump"><router-link to="/login/zc"></router-link></p>
        </div>
    </div>
</template>

<script>
import "@/assets/reset.css";
import area from "@/assets/area.js";
import local from "@/utils/local.js";
export default {
    data() {
        return {
            pwdisadmin:true,
            mobile:"",
            pwd:""
        };
    },
    beforeRouteEnter(to,from,next) {
        let g = local.get('',true)
        if(g!= null){
            next('/')
        }else{
            next()
        }
        
    },
    mounted() {

    },
    methods: {
        back(){
           this.$router.back()
        },
       btn(){
           if(this.mobile=="" || this.pwd==""){
               this.$toast.fail('')
               return false;
           }
           this.login()
       },
       login(){
           this.$axios({
               url:"https://api.it120.cc/small4/user/m/login?deviceId=007&deviceName=monkey",
               params:{
                   mobile:this.mobile,
                   pwd:this.pwd
               }
           }).then(res=>{
               if(res.data.code != 0){
                   this.$toast.fail(res.data.msg)
                   return false;
               }
               console.log(res.data.data)
               local.set('',res.data.data,true)
               this.$router.back()
            //    this.$toast.success('')
           })
       }
    }
};
</script>

<style scoped  lang="scss">
.body {
  width: 90%;
  margin: 0 auto;
  .inp {
    width: 100%;
    border: 1px solid #f5f5f5;
    background-color: #f5f5f5;
    border-radius: 0.2rem;
    margin-bottom: 0.4rem;
    img {
      width: 3rem;
    }
  }
  .jump{
    width: 100%;
    height: 50px;
    line-height: 50px;
    text-align: center;
  }
}
</style>


