<template>
  <div>
    <!--  -->
    <van-nav-bar title="" left-text="" left-arrow @click-left="onClickLeft" />
    <!--  -->
    <ul>
      <li v-for="(item,index) in shopList" :key="index">
        <van-checkbox v-model="item.checked" />
        <van-card
          :num="item.num"
          :price="item.price"
          :title="item.name"
          :thumb="item.pic"
          class="li_items"
        >
        <!--  -->
          <template #num>  
            <van-stepper v-model="item.num" />
          </template>
        </van-card>
      </li>
    </ul>

    <!--  -->
    <van-submit-bar :price="allPrice" button-text="" @submit="sub">
      <van-checkbox v-model="checked" @click="allC"></van-checkbox>
    </van-submit-bar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      checked: true,
      shopList: [],
      money:0
    };
  },
  created() {},
  mounted() {
    this.shopList = this.$store.state.cartList;
  },
  methods: {
    onClickLeft() {
      //
      this.$router.back();
    },
    allC(){  //
        
             this.shopList.forEach(item=>{
                  if(this.checked){
                      item.checked = true
                  }else{
                     item.checked = false
                  }
             })
        
    },
    sub(){
        this.$store.commit('submitC',this.allPrice)
        this.$toast.loading('')
        setTimeout(()=>{
           this.$router.push('/submit')
        },2000)
    }
  },
  watch:{
      shopList:{
          handler(value){
              let num = 0
            value.forEach(item => {
                 if(item.checked){
                      num+=1
                 } 
             });
             if(num == value.length){
                 this.checked = true
             }else{
                this.checked = false
             }

             //vuex
             this.$store.commit('fuzhi',this.shopList)
          },deep:true
      }
  },
  computed:{
        allPrice(){
            let money = 0
            this.shopList.map(item=>{
                if(item.checked){
                   money += item.num*item.price
                }
            })
            return money*100;
        }
  }
};
</script>

<style scoped lang="scss">
ul {
  width: 100%;
  padding: 0 10px;
  box-sizing: border-box;
  li {
    width: 100%;
    display: flex;
    .li_items {
      flex: 1;
    }
  }
}
</style>



