import Vue from 'vue'
import Router from 'vue-router'
import zc from '@/views/login/zc'
import dl from '@/views/login/dl'
import first from '@/views/first'
import two from '@/views/two'
import shopcar from '@/views/shopcar'
import me from '@/views/me'
import home from '@/views/home'
import cutList from '@/views/list/cutList'
import shopList from '@/views/list/shopList'
import fenleiX from '@/views/goods/fenleiX'
import search from '@/views/goods/search'
import shopXiang from '@/views/goods/shopXiang'
import onlyX from '@/views/goods/onlyX'
import details from '@/views/details/details'
import submit from '@/views/details/submit'
import pay from '@/views/details/pay'
import cutDetails from '@/views/details/cutDetails'
import cutsure from '@/views/details/cutsure'
import detailsFen from '@/views/details/detailsFen'
import myorder from '@/views/order/myorder'
import localList from '@/views/location/localList'
import localCreate from '@/views/location/localCreate'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      redirect:"/first",
      component: home,
      meta: {
        title: ""
      },
      children: [
        {
          path: '/first',
          name: 'first',
          component: first,
          meta: {
            title: ""
          }
        },
        {
          path: '/two',
          name: 'two',
          component: two,
          meta: {
            title: ""
          }
        }
        ,
        {
          path: '/shopcar',
          name: 'shopcar',
          component: shopcar,
          meta: {
            title: ""
          }
        }
        ,
        {
          path: '/me',
          name: 'me',
          component: me,
          meta: {
            title: ""
          }
        }
      ]
    },
    { //
      path: '/login/zc',
      name: 'zc',
      component: zc,
      meta: {
        title: ""
      }
    },
    {  //
      path: '/login/dl',
      name: 'dl',
      component: dl,
      meta: {
        title: ""
      }
    }
    ,
    {  //
      path: '/cutList',
      name: 'cutList',
      component: cutList,
      meta: {
        title: ""
      }
    }
    ,
    {  //
      path: '/shopList',
      name: 'shopList',
      component: shopList,
      meta: {
        title: ""
      }
    }
    ,
    {  //
      path: '/fenleiX',
      name: 'fenleiX',
      component: fenleiX,
      meta: {
        title: ""
      }
    }
    ,
    {  //
      path: '/search',
      name: 'search',
      component: search,
      meta: {
        title: ""
      }
    }
    ,
    {  //
      path: '/shopXiang',
      name: 'shopXiang',
      component: shopXiang,
      meta: {
        title: ""
      }
    }
    ,
    {  //
      path: '/onlyX',
      name: 'onlyX',
      component: onlyX,
      meta: {
        title: ""
      }
    }
    ,
    {  //
      path: '/details',
      name: 'details',
      component: details,
      meta: {
        title: ""
      }
    },
    {  //
      path: '/submit',
      name: 'submit',
      component: submit,
      meta: {
        title: ""
      }
    },
    {  //
      path: '/pay',
      name: 'pay',
      component: pay,
      meta: {
        title: ""
      }
    }
    ,
    {  //
      path: '/cutDetails',
      name: 'cutDetails',
      component: cutDetails,
      meta: {
        title: ""
      }
    }
    ,
    {  //
      path: '/cutsure',
      name: 'cutsure',
      component: cutsure,
      meta: {
        title: ""
      }
    }
    ,
    {  //
      path: '/detailsFen',
      name: 'detailsFen',
      component: detailsFen,
      meta: {
        title: ""
      }
    }
    ,
    {  //
      path: '/myorder',
      name: 'myorder',
      component: myorder,
      meta: {
        title: ""
      }
    }
    ,
    {  //
      path: '/localList',
      name: 'localList',
      component: localList,
      meta: {
        title: ""
      }
    }
    ,
    {  //
      path: '/localCreate',
      name: 'localCreate',
      component: localCreate,
      meta: {
        title: ""
      }
    }
  ]
})



