<template>
  <div class="box">
    <!--  -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="(item,index) in bannerList" :key="index" class="item">
        <img :src="item.pic" alt />
      </van-swipe-item>
    </van-swipe>
    <!--  -->
    <div class="present">
        <p v-html="present.name"></p>
        <p v-html="present.characteristic"></p>
        <p>
            <ul>
                <li>
                    <span style="color:red">:{{ present.minPrice }}</span>
                    <span style="color:#ccc;font-size:12px;">:{{ present.originalPrice }}</span>
                </li>
                <li style="color:#ccc;font-size:12px;">
                    {{ present.stores }}
                </li>
            </ul>
        </p>
    </div>
    <!--  -->
    <div class="content">
       <van-tabs v-model="active">
            <van-tab title="">
                <div v-html="this.shopList"></div>
            </van-tab>
            <van-tab title=""></van-tab>
        </van-tabs>
    </div>
    <!--  -->
    <van-sku
      v-model="show"
      :sku="sku"
      :goods="goods"
      @add-cart="addCart"
      @stepper-change="numCart"
      @sku-selected="skuSelected"
    />


    <!--  -->
    <van-icon name="arrow-left" class="base" @click="base"></van-icon>
    <!--  -->
    <van-goods-action>
        <van-goods-action-icon icon="chat-o" text="" color="#07c160" />
        <van-goods-action-icon icon="cart-o" text="" @click="jump"/>
        <van-goods-action-icon icon="star" text="" color="#ff5000" />
        <van-goods-action-button type="warning" text="" @click="show = true"/>
        <van-goods-action-button type="danger" text="" />
    </van-goods-action>
  </div>
</template>

<script>
import local from '@/utils/local'
export default {
  data() {
    return {
      // 
      bannerList: [],
      active: 0,
      //
      shopList: "",
      //
      present:{},
      //
      show: false,
       sku:{
        //sku
        tree:[
          {
            k: "",
            k_s: "c1",
            v:[
              {id:1,name:""},
              {id:2,name:""},
              {id:3,name:""},
            ]
          }
        ],
        list:[
          {id:1000,c1:1,price:2000,stock_num:12},
          {id:1000,c1:2,price:30000,stock_num:15},
          {id:1000,c1:3,price:50000,stock_num:18},
        ],
        price: 1000,
        stock_num: 100,
        },
        //
        goods: {
          picture: "",
        },
        //sku
        nums:1, //
        arr:[],//vuexvuex
        guige:"", //
      };

  },
  created() {},
  mounted() {
    this.xuan();
  },
  methods: {
    xuan() {
      this.$axios({
        url: "https://api.it120.cc/small4/shop/goods/detail",
        params: { id: this.$route.query.id }
      }).then(res => {
        console.log(res.data.data);
        this.bannerList = res.data.data.pics;
        this.shopList = res.data.data.content;
        this.present = res.data.data.basicInfo
        this.setSku(res.data.data.properties)
        this.goods.picture = res.data.data.basicInfo.pic
      });
    },
    base(){
        this.$router.back()
    },
    setSku(property){
      if(property == undefined){    //
        return false; 
      }
      
      let tree = []
      let list = []
      property.forEach(element => {   //
          let obj = new Object()
          obj.k_s = `k_${element.id}`
          obj.k = element.name
          obj.v = element.childsCurGoods
          tree.push(obj)
      });
      this.sku.tree = tree
      
      tree.forEach(item=>{
         item.v.forEach(element=>{
             let obj = new Object()
             obj.id = item.k_s.split("_")[1]
             obj[item.k_s] = element.id
             obj.price = 1000
             obj.stock_num = 120
             list.push(obj)
         })
      })
      this.sku.list = list
      
    }, //
    addCart(){ 
        let loginStatus = local.get('')
        if(loginStatus == null){
            this.$toast.fail('')
            this.$router.push('/login/dl')
            return false;
        }
        let obj = new Object()
        obj.id = this.present.id
        obj.num = this.nums
        obj.price = this.present.originalPrice
        obj.name = this.present.name
        obj.checked = true
        obj.pic = this.present.pic
        obj.propertyId = this.guige
        this.$store.commit('jumpShop',obj)
        this.show = false

    },
    numCart(value){ //
        this.nums = value
    },
    jump(){
      this.$router.push({
        path:'/shopcar'
      })
    },//
    skuSelected(value){
          this.guige = `${value.skuValue.propertyId}:${value.skuValue.id}`
    }
  }
};
</script>

<style  lang="scss">

.box {
    position: relative;
  width: 100%;
//   padding: 10px;
  box-sizing: border-box;
  background: #f0f0f0;
  //
  .my-swipe {
  width: 100%;
  .item {
    width: 100%;
    img {
      width: 100%;
      height: 350px;
    }
  }
  }
  //
  .content {
      background-color: white;
      width: 100%;
      padding: 10px;
      margin-top:10px;
       box-sizing: border-box;
    p {
      width: 100% !important;
      img {
        width: 100% !important;
        height: 30% !important;
      }
    }

    span{
        margin-top: 10px;
        // text-indent: 20px;
    }
  }
  .base{
      background-color: white;
      width: 30px;
      height: 30px;
      background-color: #f0f0f0;
      border-radius: 50%;
      text-align: center;
      line-height: 30px;
      position: absolute;
      top:10px;
      left:10px;
  }
}
.present{
    background-color: white;
    width: 100%;
    height: 100px;
    padding: 10px;
    box-sizing: border-box;
    ul{
        width: 100%;
        display: flex;
        justify-content: space-between;
    }
    p{
        line-height: 20px;
    }
    p:nth-child(2){
        font-size: 12px;
        color:#ccc;
        margin-bottom: 15px;
    }
}
</style>



